<template>
	<view class="">
		<view class="" style="">
			<!-- view_t -->
			<popUp ref="subBind"></popUp>
			<load ref="tekygo"></load>
			<view class=" status_bar bgc-o-r" ></view>
			<view class="df-jcc bgc-o-r" style="width: 100%;   position: relative;">
				<view v-if="tekyFlag== 'tekyFlag' ? false : true" class="" style=""> 
					<image src="../../static/img/Tekygoicon.png" mode="widthFix" 
							style="width: 106rpx; "></image>
				</view>
				<view class="df-jcc-aic Teky1" style="right: 44rpx;" @click="saoYiSao"
					hover-class="hover" hover-start-time=0 hover-stay-time=150 >
					<image src="../../static/img/xiaoTubiao/+.png" mode="" 
							style="width: 45rpx; height: 41rpx;"></image>
				</view>
				<view v-if="false" class="df-jcc-aic Teky1" style="right: 44rpx; ">
					<image src="../../static/img/xiaoTubiao/laBa.png" mode="" 
							style="width: 35rpx; height: 43rpx;"></image>
				</view>
			</view>  
			<view class="" v-if="tekyFlag== 'tekyFlag' ? false : true">
				<image class="bgc-picture1" src="../../static/mlestones/All.png" mode="aspectFill"></image>
			</view>
		</view>
		
		<view class="showmodel" v-if="ifGo">
			<view class="" style="height: 30vh;"></view>
			<view class="bgc_FFF" 
			style="margin: 0 78rpx; border-radius: 51rpx; height: 500rpx; border: 1rpx solid #9ec0d7;">
				<view class="df" style="justify-content: flex-end; ">
					<image @click="newclose" src="../../static/fristPage/qx.png" mode="widthFix" 
					style="width: 79rpx; margin: 18rpx 18rpx 0 0;"></image>
				</view>  
				<view class="ta-c color-H0f649c f49rpx fw800" style="margin: 32rpx 0 43rpx; height: 43rpx;">
					{{title}}
				</view>
				<view class="ta-c color-H0f649c f33rpx" style="margin: 0 20rpx 46rpx;  height: 95rpx;">
					{{content}}
				</view>
				<view class="FFF f33rpx ta-c bgc-o-r fw800" @click="goLogin"
				style="line-height: 84rpx; margin: 0 42rpx; height: 84rpx; border-radius: 42rpx; ">
					{{key}} 
				</view>
			</view>
		</view>
	</view>

</template>

<script>
	
	export default{
		props:{
			tekyFlag:String
		},
		data(){
			return{
				height:40,
				iStatusBarHeight:0,
				result2:'',
				id:'',
				serial:'',
				ifBind:false,
				ifGo:false,
				title:'',
				content:'',
				key:'',
			}
		},
		
		methods:{
			newonShow(title,content,key){
				this.ifGo = true
				this.title = title
				this.content = content
				this.key = key
			},
			
			newclose(){
				this.ifGo = false
			},
			goLogin(){
				uni.reLaunch({
					url:'../welcomePage/inputEmail'
				})
			},

			async saoYiSao(){ 
				let that = this
				let detaile = ''
				let isLogin = uni.getStorageSync('isLogin')
				if(!isLogin){
					// console.log('未登录');
					this.newonShow('Please Login',"You haven't logged in yet. Please log in",'Login')
				}else{
					// console.log('已登录');
					uni.scanCode({
						onlyFromCamera: true,                   
						success(res) {  
							console.log(res);
							detaile = res.result
							if(detaile.startsWith('https://www.tekygo.com')){
								uni.redirectTo({
									url:'../subscription/subscription'
								})
								// that.$refs.tekygo.openLoad()
								// that.getUrl()
								// console.log(res);
								//获取二维码内容开头为https://www.tekygo.com
								console.log('二维码');
							}else if(detaile.length == 32){
								//设备id
								console.log('设备id');
								that.id = res.result
								uni.setStorageSync("id",that.id)
								console.log(that.id);
								that.scanCodes()
							}else{
								this.$refs.subBind.openPopup('Invalid code')
							}
						}
					})
				}

			},
			async getUrl(){
				const res = await this.$myRequest({
					url:'/api/sub/get'
				})
				console.log(res);
				console.log(res.data.succeeded);
				if(res.data.succeeded){
					this.$refs.tekygo.closeLoad()
					plus.runtime.openURL(res.data.data);
				}else{
					this.$refs.subBind.openPopup(res.data.errmsg)
				}
			},
			
			//扫码绑定设备
			async scanCodes(){
				// var that = this
				const res = await this.$myRequest({
					url:'/api/device/bind'+'?processId='+ this.id,
				})
				console.log(res); 
				if(res.data.succeeded){
					console.log('绑定成功');
					this.$refs.subBind.openPopup('Binding success!')
					uni.setStorageSync("isBind",true)
					this.bindSyntony()
				}else{
					console.log('绑定失败');
					this.$refs.subBind.openPopup(res.data.errmsg)
				}
			}, 
			
			async bindSyntony(){
				const res = await this.$myRequest({
					url:'/api/device/list' + '?page=1' +'&limit=1',
				})
				console.log('展示');
				console.log(res)
				// console.log(res.data.data.records[0].serial);
				// uni.setStorageSync("serial",res.data.data.records[0].serial)
				// this.serial = res.data.data.records[0].serial
			},  
		},
		beforeCreate() {

		}
	}
</script>

<style scoped>
	
	.Teky1{
		position: absolute; 
		top: 29rpx; 
		width: 60rpx; 
		height: 60rpx;
	}
	.bgc{
		background-color: #eb5d2d;
	}
	.bgc-picture1{
		 z-index:-22; 
		 width:750rpx; 
		 height:100vh; 
		 position: fixed; 
		 top:0; 
	}
	.showmodel{
		z-index: 20;
		background: rgba(0,0,0,0.5);
		position: fixed;
		left: 0;
		right: 0;
		bottom: -100rpx; 
		top: 0;
	}

</style>
