			return{
				timers:null,
				i:0
			}
		},
		methods:{

			
			async getToken(){
				let email = uni.getStorageSync('Email')
				let possword = uni.getStorageSync('Password')
				if(email != '' && possword != ''){
					const {data:res} = await this.$myRequest({
						method:'POST',
						url:'/api/auth/login',
						data:{
							"email":email,
							"password":possword
						}
					}) 
					console.log(res)
					console.log('一次')
					if(res.data){
						uni.setStorageSync('token',res.data.token)
					}
				}else{
					return false
				}
			}
		},
