import 'uni-pages';import Vue from 'vue'
import App from './App'

//uni.request
import {myRequest} from './util/uniRequest.js'
Vue.prototype.$myRequest = myRequest

import netWork from './util/bai.js'
Vue.prototype.$netWork = netWork

// 全局引入组件
import TekyGoTop from './pages/components/TekyGo-Top.vue'
import TekyGo from './pages/components/TekyGo.vue'
Vue.component('TekyGoTop',TekyGoTop)
Vue.component('TekyGo',TekyGo)

// 弹框组件
import popUp from './pages/components/popUp.vue'
Vue.component('popUp',popUp)

// 进度条组件
import load from './pages/components/load.vue'
Vue.component('load',load)

Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
    ...App
})
app.$mount()